.list {
    list-style-type: none;
}

.list-item {
    border: 1px solid lightgrey;
    padding: 15px;
    margin: 10px;
}

.hasChildren {
    cursor: pointer;
    padding: 10px 20px;
    background-color: lightblue;
    display: inline-block;
    border-radius: 7px;
}

.noChildren {
    padding: 10px 20px;
    background-color: lightgrey;
    display: inline-block;
    border-radius: 7px;
}

.buttons {
    display: flex;
    gap: 5px;
    margin: 10px 0 0;
}

.button {
    all: unset;
    background-color: #eee;
    padding: 3px 10px;
    border-radius: 7px;
    cursor: pointer;
}

.input {
    all: unset;
    border: 1px solid lightgrey;
    padding: 3px 5px;
    border-radius: 5px;
    margin: 15px 15px 0 0;
}
